import React, { useCallback, useEffect, useState } from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import ApiService from "../services/ApiService";
import {
  Box,
  Button,
  Container,
  Grid,
  Modal,
  TextField,
  Typography,
} from "@mui/material";
import { useDispatch } from "react-redux";
import { deletePost } from "../store/slices/postSlice";

const PostDetail = () => {
  const { id } = useParams();
  const [post, setPost] = useState(null);
  const [password, setPassword] = useState("");
  const [showModal, setShowModal] = useState(false);
  const [actionType, setActionType] = useState(""); // "edit"  "delete"
  const [errorMessage, setErrorMessage] = useState("");
  const location = useLocation(); //   
  const navigate = useNavigate();
  const dispatch = useDispatch();

  //  
  const modalStyle = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    borderRadius: 2,
    boxShadow: 24,
    p: 4,
  };

  //   
  const fetchPostDetails = useCallback(
    async (password) => {
      try {
        const response = await ApiService.fetchPostDetails(id, password);
        setPost(response.data);
      } catch (error) {
        setErrorMessage("Error fetching post details");
        console.error("Error fetching post details: ", error);
      }
    },
    [id]
  );

  // 
  useEffect(() => {
    if (location.state && location.state.password && id) {
      fetchPostDetails(location.state.password);
    }
  }, [id, location.state, fetchPostDetails]);

  // /     
  const handleActionClick = (type) => {
    setActionType(type);
    setShowModal(true); // 
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  //     
  const handleSubmit = async () => {
    setErrorMessage("");
    try {
      //
      if (actionType === "delete") {
        // await ApiService.deletePost(id, password);
        // navigate(`/board/${post.board_id}`); //     
        const resultAction = await dispatch(
          deletePost({ postId: id, password })
        );
        if (deletePost.fulfilled.match(resultAction)) {
          navigate("/posts"); //     
        } else {
          setErrorMessage(" .  .");
        }
      }
      //
      else if (actionType === "edit") {
        //    ( boardId  )
        navigate(`/edit-post/${id}`, {
          state: { password },
        });
      }
    } catch (error) {
      console.error("Error deleting or editing post: ", error);
    }
  };

  if (!post) return <div>Loading...</div>;

  return (
    <Container>
      <Typography variant="h4" gutterBottom>
        {post.title}
      </Typography>
      <Typography variant="body1">: {post.user_name} </Typography>
      <Typography variant="body1">
        : {new Date(post.posted_at).toLocaleDateString()}
      </Typography>
      <Typography variant="body2"> {post.content} </Typography>

      <Grid container spacing={2}>
        <Grid item>
          <Button
            onClick={() => {
              handleActionClick("edit");
            }}
          >
            
          </Button>
        </Grid>
        <Grid item>
          <Button
            onClick={() => {
              handleActionClick("delete");
            }}
          >
            
          </Button>
        </Grid>
        <Grid item>
          <Button
            variant="outlined"
            onClick={() => navigate(`/board/${post.board_id}`)}
          >
              {location.state.boardId}
          </Button>
        </Grid>
      </Grid>

      <Modal open={showModal} onClose={() => setShowModal(false)}>
        <Box sx={modalStyle}>
          <Typography variant="h6" gutterBottom>
             
          </Typography>
          <TextField
            type="password"
            label=""
            fullWidth
            value={password}
            onChange={handlePasswordChange}
            error={!!errorMessage}
            helperText={errorMessage}
          />
          <Box sx={{ mt: 2, display: "flex", justifyContent: "space-between" }}>
            <Button variant="contained" color="primary" onClick={handleSubmit}>
              
            </Button>
            <Button
              variant="contained"
              color="primary"
              onClick={() => setShowModal(false)}
            >
              
            </Button>
          </Box>
        </Box>
      </Modal>
    </Container>
  );
};

export default PostDetail;
