import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Box, Button, Modal, TextField, Typography } from "@mui/material";
import { useDispatch } from "react-redux";
import { fetchPostDetails } from "../store/slices/postSlice";

const Post = ({ post }) => {
  const [showModal, setShowModal] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();
  const dispatch = useDispatch();

  //  
  const modalStyle = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    borderRadius: 2,
    boxShadow: 24,
    p: 4,
  };

  //      
  const handleClick = () => {
    setShowModal(true);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  //  
  const handleSubmit = async () => {
    setErrorMessage(""); // 
    try {
      // await ApiService.fetchPostDetails(post.id, password);
      // //      
      // navigate(`/post/${post.id}`, {
      //   state: { password },
      // });
      const resultAction = await dispatch(
        fetchPostDetails({ postId: post.id, password })
      );
      if (fetchPostDetails.fulfilled.match(resultAction)) {
        navigate(`/post/${post.id}`, { state: { password } });
      } else {
        setErrorMessage(" . .");
      }
    } catch (error) {
      console.error("Error fetching post details: ", error);
      setErrorMessage(" .  .");
    }
  };

  return (
    <Box>
      <Typography variant="body2" onClick={handleClick}>
        {post.title} | : {post.user_name} | :{" "}
        {new Date(post.posted_at).toLocaleDateString()}
      </Typography>

      <Modal open={showModal} onClose={() => setShowModal(false)}>
        <Box sx={modalStyle}>
          <Typography variant="h6" gutterBottom>
             
          </Typography>
          <TextField
            type="password"
            label=""
            fullWidth
            value={password}
            onChange={handlePasswordChange}
            error={!!errorMessage}
            helperText={errorMessage}
          />
          <Box sx={{ mt: 2, display: "flex", justifyContent: "space-between" }}>
            <Button variant="contained" color="primary" onClick={handleSubmit}>
              
            </Button>
            <Button
              variant="contained"
              color="primary"
              onClick={() => setShowModal(false)}
            >
              
            </Button>
          </Box>
        </Box>
      </Modal>
    </Box>
  );
};

export default Post;
